body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* visually hidden snippet */
.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/* global styles */
.App {
  margin: 0 auto;
  min-height: 100vh;
  background:-webkit-linear-gradient(bottom,rgba(255,255,255,0.3),rgba(255, 255, 255, 0.3)),url(/glenMccannProject05/static/media/story-background.b49c07ff.jpg);
  background:linear-gradient(0deg,rgba(255,255,255,0.3),rgba(255, 255, 255, 0.3)),url(/glenMccannProject05/static/media/story-background.b49c07ff.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  max-width: 1600px;
  font-size: 125%;
  text-align: center;
}

.wrapper {
  max-width: 1600px;
}
/* header styles */

header {
    background-size: cover;
    background-position: center;
    height:30vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

h1 {
    margin: 0px;
    color: white;
    font-family: 'Amatic SC', sans-serif;
    font-size: 5rem;
    padding: 15px;
    color: #5C6851;
}

/* media queries */
@media (max-width: 400px) {
    h1 {
        font-size: 4rem;
    }
}
/* styles for description section */
.description {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    line-height: 1.6;
}

.content {
    color: #353535;
    padding: 5%;
    max-width: 65%;
    padding-top: 0;
    padding-bottom: 0;
}

.content p {
    margin: 0;
    font-family: 'Lato', sans-serif;
    font-size: 1.4rem;
}
/* story section styles */

.story {
    background-size: cover;
    margin: 0;
    padding: 5% 10%;
}

/* entry styles */
ul {
    padding: 0;
    margin: 0;
}

li {
    font-family: 'Lato', sans-serif;
    list-style: none;
    border: 1px dotted #5C6851;
    margin: 0 auto;
    width: 90%;
    max-width: 800px;
    margin-bottom: 10px;
    padding: 10px 25px;
}

li:nth-child(odd) {
    background: #dedcc391;
}

li:nth-child(even) {
    background: rgba(175, 169, 211, 0.5);
}

/* story styles */
p.body {
    color: #353535;
    padding-top: 5px;
    text-align: left;
    margin: 0;
    line-height: 1.6;
}

/* attribution styles */
p.attribution {
    text-align: right;
    opacity: 0.5;
    color: #353535;
    padding-top: 20px;
    margin: 0;
}


/* media queries */
@media (max-width: 400px) {
    p {
        font-size: 1rem;
    }
}
/* form section styles */
.formSection {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-bottom: 25px;
}

.formContainer {
    max-width: 800px;
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: rgba(255, 255, 255, 0.75);
    border: 5px solid #afa9d3;
    padding: 20px 0;
}
/* form styles */
.form {
    width: 90%;
}

form {
    font-family: 'Lato', sans-serif;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 25px;
}

form p {
    color: black;
    text-align: left;
}

/* input field styles */
form input[type=text],
form textarea {
    width: 100%;
    border: 1px solid #5C6851;
    color: black;
    font-size: 1rem;
    padding: 5px;
    padding-left: 10px;
    margin-bottom: 1.5%;
    font-family: 'Lato', sans-serif;
    line-height: 1.6;
}

textarea {
    resize: none;
}

:required {
    border: 1px solid #5C6851;
    box-shadow: none;

}

.storyField {
    min-height: 200px;
    padding: none;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin-bottom: 20px;
}

input[type=text].smallField {
    display: inline-block;
    width: 49%;
    margin: 0;
}

/* buttons */
button {
    width: 45%;
    border: 2px solid #5C6851;
    border-radius: 5px;
    background: none;
    font-size: 1rem;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    text-transform: uppercase;
    color: #5C6851;
    font-family: 'Lato', sans-serif;
}

button:hover {
    background: #5C6851;
    color: white;
}

/* media queries */
@media (max-width: 400px) {
    p {
        font-size: 1rem;
    }

    input[type=text].smallField {
        width: 100%;
        margin-bottom: 10px;
    }

    button {
        width: 100%;
        padding: 5px;
    }
}
